/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  color: #333;
}

.App {
  min-height: 100vh;
}

/* Connect Wallet Screen */
.connect-wallet {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
  color: white;
  padding: 2rem;
}

.connect-wallet h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.3);
}

.connect-wallet p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.connect-wallet .error {
  color: #ff6b6b;
  background: rgba(255, 107, 107, 0.1);
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid rgba(255, 107, 107, 0.3);
}

/* Header */
.header {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  padding: 1.5rem 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.header h1 {
  color: #2c3e50;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  text-align: center;
}

.wallet-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
  max-width: 1400px;
  margin: 0 auto;
}

.wallet-info p {
  background: rgba(52, 152, 219, 0.1);
  padding: 0.75rem 1rem;
  border-radius: 8px;
  border-left: 4px solid #3498db;
  font-size: 0.9rem;
  word-wrap: break-word;
  word-break: break-all;
  overflow-wrap: break-word;
  hyphens: auto;
  line-height: 1.4;
}

.wallet-info strong {
  color: #2c3e50;
}

/* Loading message */
.loading-message {
  background: rgba(52, 152, 219, 0.1);
  border: 1px solid #3498db;
  border-radius: 8px;
  padding: 1rem;
  margin: 1rem 2rem;
  text-align: center;
  color: #2c3e50;
}

/* Time remaining and expiration styles */
.time-remaining {
  color: #27ae60;
  font-weight: bold;
}

.expired {
  color: #e74c3c;
  font-weight: bold;
}

/* Status indicators */
.status-fulfilled {
  color: #27ae60;
  font-weight: bold;
}

.status-cancelled {
  color: #e74c3c;
  font-weight: bold;
}

/* Highest bidder indicator */
.highest-bidder {
  background: linear-gradient(45deg, #f39c12, #f1c40f);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-weight: bold;
  text-align: center;
  margin: 0.5rem 0;
  box-shadow: 0 2px 8px rgba(243, 156, 18, 0.3);
}

/* Owner badge */
.owner-badge {
  background: linear-gradient(45deg, #3498db, #2980b9);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-weight: bold;
  text-align: center;
  margin: 0.5rem 0;
  box-shadow: 0 2px 8px rgba(52, 152, 219, 0.3);
}

.loading-message p {
  font-size: 1.1rem;
  font-weight: 500;
}

/* Style for address values specifically */
.wallet-info .address-value,
.auction-card .address-value {
  font-family: 'Courier New', monospace;
  font-size: 0.8rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.wallet-info .address-value:hover,
.auction-card .address-value:hover {
  background: rgba(52, 152, 219, 0.2);
  border-radius: 4px;
  padding: 2px 4px;
}

/* Event status styling */
.events-status {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-weight: bold;
  font-size: 0.8rem;
}

.events-status.connected {
  background: rgba(46, 204, 113, 0.2);
  color: #27ae60;
  border: 1px solid rgba(46, 204, 113, 0.3);
}

.events-status.disconnected {
  background: rgba(231, 76, 60, 0.2);
  color: #e74c3c;
  border: 1px solid rgba(231, 76, 60, 0.3);
}

/* Success and error message styling */
.success-message,
.error-message {
  padding: 1rem 2rem;
  margin: 1rem 0;
  border-radius: 8px;
  font-weight: bold;
  text-align: center;
  animation: slideIn 0.3s ease;
}

.success-message {
  background: rgba(46, 204, 113, 0.2);
  color: #27ae60;
  border: 1px solid rgba(46, 204, 113, 0.3);
}

.error-message {
  background: rgba(231, 76, 60, 0.2);
  color: #e74c3c;
  border: 1px solid rgba(231, 76, 60, 0.3);
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Tooltip for full address on hover */
.address-value:hover::after {
  content: attr(data-full-address);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 0, 0, 0.9);
  color: white;
  padding: 0.5rem;
  border-radius: 4px;
  font-size: 0.7rem;
  white-space: nowrap;
  z-index: 1000;
  pointer-events: none;
  margin-bottom: 5px;
}

.address-value:hover::before {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  border: 5px solid transparent;
  border-top-color: rgba(0, 0, 0, 0.9);
  z-index: 1000;
  pointer-events: none;
  margin-bottom: -5px;
}

.owner-badge {
  background: rgba(46, 204, 113, 0.1) !important;
  border-left-color: #2ecc71 !important;
  color: #27ae60;
  font-weight: bold;
}

/* Error Message */
.error-message {
  background: #ff6b6b;
  color: white;
  padding: 1rem 2rem;
  margin: 1rem 2rem;
  border-radius: 8px;
  text-align: center;
  font-weight: 500;
  box-shadow: 0 2px 10px rgba(255, 107, 107, 0.3);
}

/* Success Message */
.success-message {
  background: #2ecc71;
  color: white;
  padding: 1rem 2rem;
  margin: 1rem 2rem;
  border-radius: 8px;
  text-align: center;
  font-weight: 500;
  box-shadow: 0 2px 10px rgba(46, 204, 113, 0.3);
}

/* Main Content */
.main-content {
  max-width: 1400px;
  margin: 0 auto;
  padding: 2rem;
}

/* Section Styles */
section {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

section h2 {
  color: #2c3e50;
  font-size: 2rem;
  margin-bottom: 1.5rem;
  text-align: center;
  border-bottom: 3px solid #3498db;
  padding-bottom: 0.5rem;
}

/* Form Styles */
.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #2c3e50;
}

.form-group input[type="text"],
.form-group input[type="number"] {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
}

.form-group input[type="checkbox"] {
  margin-right: 0.5rem;
}

/* Button Styles */
button {
  background: linear-gradient(135deg, #3498db, #2980b9);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
}

button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(52, 152, 219, 0.4);
}

button:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.cancel-btn {
  background: linear-gradient(135deg, #e74c3c, #c0392b);
  box-shadow: 0 4px 15px rgba(231, 76, 60, 0.3);
}

.cancel-btn:hover:not(:disabled) {
  box-shadow: 0 6px 20px rgba(231, 76, 60, 0.4);
}

.complete-btn {
  background: linear-gradient(135deg, #2ecc71, #27ae60);
  box-shadow: 0 4px 15px rgba(46, 204, 113, 0.3);
}

.complete-btn:hover:not(:disabled) {
  box-shadow: 0 6px 20px rgba(46, 204, 113, 0.4);
}

.destroy-btn {
  background: linear-gradient(135deg, #e67e22, #d35400);
  box-shadow: 0 4px 15px rgba(230, 126, 34, 0.3);
}

.destroy-btn:hover:not(:disabled) {
  box-shadow: 0 6px 20px rgba(230, 126, 34, 0.4);
}

/* Auction Grid */
.auctions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
}

.auction-card {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.auction-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.auction-card h3 {
  color: #2c3e50;
  font-size: 1.3rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid #3498db;
  padding-bottom: 0.5rem;
}

.auction-card p {
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.auction-card strong {
  color: #2c3e50;
}

.highest-bidder {
  background: rgba(46, 204, 113, 0.1);
  color: #27ae60;
  padding: 0.5rem;
  border-radius: 6px;
  font-weight: bold;
  text-align: center;
  margin: 1rem 0;
}

/* Bid Form */
.bid-form {
  margin: 1rem 0;
  padding: 1rem;
  background: rgba(52, 152, 219, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(52, 152, 219, 0.2);
}

.bid-form input {
  width: 100%;
  margin-bottom: 0.5rem;
}

.bid-form button {
  width: 100%;
}

/* Auction Actions */
.auction-actions {
  margin-top: 1rem;
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.auction-actions button {
  flex: 1;
  min-width: 120px;
}

/* Status Badges */
.status-fulfilled {
  background: #2ecc71;
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: bold;
}

.status-cancelled {
  background: #e74c3c;
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: bold;
}

/* Claim Refunds */
.claim-refunds {
  background: rgba(241, 196, 15, 0.1);
  border: 1px solid rgba(241, 196, 15, 0.3);
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 1.5rem;
  text-align: center;
}

.claim-refunds h3 {
  color: #f39c12;
  margin-bottom: 1rem;
}

.claim-refunds p {
  font-size: 1.1rem;
  margin-bottom: 1rem;
  color: #2c3e50;
}

/* Admin Panel */
.admin-panel {
  background: rgba(155, 89, 182, 0.05);
  border: 1px solid rgba(155, 89, 182, 0.2);
}

.admin-actions {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

.admin-action {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  border: 1px solid #e0e0e0;
}

.admin-action h3 {
  color: #8e44ad;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.admin-action form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.admin-action input {
  padding: 0.75rem 1rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
}

.admin-action input:focus {
  outline: none;
  border-color: #8e44ad;
  box-shadow: 0 0 0 3px rgba(142, 68, 173, 0.1);
}

/* Responsive Design */
@media (max-width: 768px) {
  .header {
    padding: 1rem;
  }
  
  .header h1 {
    font-size: 2rem;
  }
  
  .wallet-info {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  
  .wallet-info p {
    font-size: 0.8rem;
    padding: 0.5rem 0.75rem;
  }
  
  .main-content {
    padding: 1rem;
  }
  
  .auctions-grid {
    grid-template-columns: 1fr;
  }
  
  .admin-actions {
    grid-template-columns: 1fr;
  }
  
  .auction-actions {
    flex-direction: column;
  }
  
  .auction-actions button {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .connect-wallet h1 {
    font-size: 2rem;
  }
  
  section {
    padding: 1rem;
  }
  
  section h2 {
    font-size: 1.5rem;
  }
  
  .auction-card {
    padding: 1rem;
  }
}

/* Loading States */
.loading {
  opacity: 0.6;
  pointer-events: none;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.auction-card {
  animation: fadeIn 0.5s ease-out;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb {
  background: #3498db;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #2980b9;
}

/* Emergency Button */
.emergency-btn {
  background: linear-gradient(135deg, #f39c12, #e67e22) !important;
}

.emergency-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #e67e22, #d35400) !important;
  box-shadow: 0 4px 12px rgba(243, 156, 18, 0.3) !important;
}

/* Bid History Button */
.history-btn {
  background: linear-gradient(135deg, #9b59b6, #8e44ad) !important;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1rem;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 0.5rem;
}

.history-btn:hover {
  background: linear-gradient(135deg, #8e44ad, #7d3c98) !important;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(155, 89, 182, 0.3);
}

/* User Bid History Section */
.user-bid-history {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  padding: 1.5rem;
  margin: 1rem 0;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.user-bid-history h3 {
  color: #2c3e50;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
}

.user-bid-history button {
  background: linear-gradient(135deg, #9b59b6, #8e44ad);
  color: white;
  border: none;
  border-radius: 6px;
  padding: 0.75rem 1.5rem;
  font-size: 0.9rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: block;
  margin: 0 auto;
}

.user-bid-history button:hover:not(:disabled) {
  background: linear-gradient(135deg, #8e44ad, #7d3c98);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(155, 89, 182, 0.3);
}

.user-bid-history button:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.modal-content {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  max-width: 800px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid rgba(52, 152, 219, 0.2);
}

.modal-header h3 {
  color: #2c3e50;
  font-size: 1.5rem;
  margin: 0;
}

.close-btn {
  background: none;
  border: none;
  font-size: 2rem;
  color: #7f8c8d;
  cursor: pointer;
  padding: 0;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.close-btn:hover {
  background: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
}

.modal-body {
  padding: 1.5rem;
}

/* Bid History Styles */
.bid-history, .user-bid-history-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.bid-item, .user-bid-item {
  background: rgba(52, 152, 219, 0.05);
  border: 1px solid rgba(52, 152, 219, 0.2);
  border-radius: 8px;
  padding: 1rem;
  transition: all 0.3s ease;
}

.bid-item:hover, .user-bid-item:hover {
  background: rgba(52, 152, 219, 0.1);
  border-color: rgba(52, 152, 219, 0.4);
  transform: translateY(-2px);
}

.bid-item p, .user-bid-item p {
  margin: 0.5rem 0;
  font-size: 0.9rem;
  line-height: 1.4;
}

.bid-item strong, .user-bid-item strong {
  color: #2c3e50;
  font-weight: 600;
}

/* Auction Navigation Styles */
.auction-navigation {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-radius: 12px;
  padding: 2rem;
  margin: 2rem auto;
  max-width: 1400px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.auction-navigation h2 {
  color: #2c3e50;
  font-size: 2rem;
  margin-bottom: 1.5rem;
  text-align: center;
}

.navigation-controls {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  align-items: start;
}

.auction-search, .auction-stats {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.auction-search label {
  font-weight: 600;
  color: #2c3e50;
  font-size: 1rem;
}

.auction-search input {
  padding: 0.75rem 1rem;
  border: 2px solid rgba(52, 152, 219, 0.3);
  border-radius: 8px;
  font-size: 1rem;
  background: rgba(255, 255, 255, 0.9);
  transition: all 0.3s ease;
}

.auction-search input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
}

.auction-search button, .auction-stats button {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #3498db, #2980b9);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(52, 152, 219, 0.3);
}

.auction-search button:hover, .auction-stats button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(52, 152, 219, 0.4);
}

.auction-search button:disabled, .auction-stats button:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.stats-display {
  background: rgba(52, 152, 219, 0.05);
  border: 1px solid rgba(52, 152, 219, 0.2);
  border-radius: 8px;
  padding: 1.5rem;
  margin-top: 1rem;
}

.stats-display h3 {
  color: #2c3e50;
  font-size: 1.3rem;
  margin-bottom: 1rem;
  text-align: center;
}

.stats-display p {
  margin: 0.5rem 0;
  font-size: 1rem;
  line-height: 1.4;
  color: #34495e;
}

.stats-display strong {
  color: #2c3e50;
  font-weight: 600;
}

/* Warning Message Styles */
.warning-message {
  background: rgba(243, 156, 18, 0.1);
  border: 1px solid rgba(243, 156, 18, 0.3);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1.5rem;
}

.blacklist-warning {
  background: rgba(231, 76, 60, 0.1);
  border: 1px solid rgba(231, 76, 60, 0.3);
  border-radius: 8px;
  padding: 0.75rem 1rem;
  color: #c0392b;
  font-weight: 600;
  text-align: center;
  margin-top: 0.5rem;
}

.warning-message p {
  color: #d68910;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.warning-message ul {
  margin-left: 1.5rem;
  color: #d68910;
}

.warning-message li {
  margin: 0.25rem 0;
  line-height: 1.4;
}

/* Responsive Design for Navigation */
@media (max-width: 768px) {
  .navigation-controls {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .auction-navigation {
    margin: 1rem;
    padding: 1.5rem;
  }
  
  .auction-navigation h2 {
    font-size: 1.5rem;
  }
}

/* New button styles */
.details-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s;
  margin-right: 0.5rem;
}

.details-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.details-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

/* Auction search styles */
.auction-search input {
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.2s;
}

.auction-search input:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
}

/* Auction details modal specific styles */
.modal-body h4 {
  color: #2c3e50;
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.modal-body p {
  margin: 0.5rem 0;
  padding: 0.5rem 0;
  border-bottom: 1px solid #f0f0f0;
}

.modal-body p:last-child {
  border-bottom: none;
}

.modal-body strong {
  color: #2c3e50;
  font-weight: 600;
}
